<template>
  <view class="box">
    <view class="cascade-container">
      <cascadeSelect
        ref="cascadeSelectRef"
        :options="areaOptions"
        :field-names="fieldNames"
        :is-full-value="true"
        @change="changeCacade"
      />
    </view>
    <view class="bottom-container">
      <button class="button reset" @click="reset">重置</button>
      <button class="button confirm" @click="confirm">确定</button>
    </view>
  </view>
</template>

<script>
import cascadeSelect from './cascadeSelect.vue'
export default {
  name: 'dropDownProjectSelect',
  components: {
    cascadeSelect,
  },
  props: {},
  data() {
    return {
      fieldNames: { label: 'name', value: 'tag_id', children: 'tag_id_arr' },
      areaOptions: [],
    }
  },
  computed: {},
  watch: {},
  created() {
    this.getTagGroupList()
  },

  methods: {
    confirm() {
      let data = this.$refs.cascadeSelectRef.getValues()
      this.$emit('confirm', data)
    },
    reset() {
      this.$refs.cascadeSelectRef.reset()
      let data = this.$refs.cascadeSelectRef.getValues()
      this.$emit('reset', data)
    },
    changeCacade(e) {
      console.log(e)
    },
    async getTagGroupList() {
      const res = await this.$api.getTagGroupList({
        page: 1,
        size: 1000,
        type: 'social_sports_project_kind',
        is_show_tag_id: 1,
      })
      res.data.list.map((e) => {
        e.tag_id = e.tag_group_id
        e.tag_id_arr = e.tag_id_arr.map((item) => {
          return {
            name: item.tag_id_str,
            tag_id: item.tag_id,
          }
        })
      })
      this.areaOptions = res.data.list
    },
  },
}
</script>

